body {
  box-sizing: border-box;
  background: #35488c;
  margin: 0;
  padding: 0;
}

.container {
  color: #222;
  text-align: center;
  background: #fff;
  border-radius: 12px;
  margin: 3rem auto;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  box-shadow: 0 8px 20px #00000014;
}

.title {
  color: #2c3e50;
  letter-spacing: 1.1px;
  margin-bottom: 1.5rem;
  font-size: 1.9rem;
  font-weight: 700;
}

.label {
  text-align: left;
  color: #34495e;
  letter-spacing: .7px;
  margin-bottom: .6rem;
  font-size: 1rem;
  font-weight: 600;
  display: block;
}

.input-date {
  color: #2c3e50;
  border: 1.8px solid #bdc3c7;
  border-radius: 8px;
  outline: none;
  width: 90%;
  margin-bottom: 1.5rem;
  padding: .6rem 1rem;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: inset 1px 1px 4px #0000000d;
}

.btn-calc {
  color: #fff;
  cursor: pointer;
  user-select: none;
  border: none;
  border-radius: 10px;
  width: 80%;
  box-shadow: 0 4px 12px #34495e80;
}

.error-msg {
  color: #e74c3c;
  letter-spacing: .6px;
  margin-top: 1.4rem;
  font-size: .95rem;
  font-weight: 600;
}

.age-result {
  color: #27ae60;
  letter-spacing: .8px;
  margin-top: 1.4rem;
  font-size: 1.15rem;
  font-weight: 600;
}

